import React, { useContext, useEffect, useState } from "react";
import { useNavigate } from "react-router-dom";
import { styled } from "@mui/material/styles";
import axios from "axios";
import {
  TextField,
  Button,
  Box,
  Checkbox,
  Typography,
  Link,
  Paper,
} from "@mui/material";
import {
  Toolbar,
  List,
  Divider,
  IconButton,
  Grid,
  Container,
} from "@mui/material";
import CssBaseline from "@mui/material/CssBaseline";
import MuiDrawer from "@mui/material/Drawer";
import MuiAppBar from "@mui/material/AppBar";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import ChevronRightIcon from "@mui/icons-material/ChevronRight";
import ChevronLeftIcon from "@mui/icons-material/ChevronLeft";
import MenuIcon from "@mui/icons-material/Menu";
import LogoutIcon from "@mui/icons-material/Logout";
import { TreeView, TreeItem } from "@mui/lab";
import { BaseUrlContext } from "../index";
import MainListItems from "../components/listitems";
import { Card, CardMedia, CardActions, CardContent } from "@mui/material";

function Copyright(props) {
  return (
    <Typography
      variant="body2"
      color="text.secondary"
      align="center"
      {...props}
    >
      {"Copyright © "}
      <Link color="inherit" href="https://mui.com/">
        Your Website
      </Link>{" "}
      {new Date().getFullYear()}
      {"."}
    </Typography>
  );
}
const drawerWidth = 240;

const AppBar = styled(MuiAppBar, {
  shouldForwardProp: (prop) => prop !== "open",
})(({ theme, open }) => ({
  zIndex: theme.zIndex.drawer + 1,
  transition: theme.transitions.create(["width", "margin"], {
    easing: theme.transitions.easing.sharp,
    duration: theme.transitions.duration.leavingScreen,
  }),
  ...(open && {
    marginLeft: drawerWidth,
    width: `calc(100% - ${drawerWidth}px)`,
    transition: theme.transitions.create(["width", "margin"], {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
  }),
}));

const Drawer = styled(MuiDrawer, {
  shouldForwardProp: (prop) => prop !== "open",
})(({ theme, open }) => ({
  "& .MuiDrawer-paper": {
    position: "relative",
    whiteSpace: "nowrap",
    width: drawerWidth,
    transition: theme.transitions.create("width", {
      easing: theme.transitions.easing.sharp,
      duration: theme.transitions.duration.enteringScreen,
    }),
    boxSizing: "border-box",
    ...(!open && {
      overflowX: "hidden",
      transition: theme.transitions.create("width", {
        easing: theme.transitions.easing.sharp,
        duration: theme.transitions.duration.leavingScreen,
      }),
      width: theme.spacing(7),
      [theme.breakpoints.up("sm")]: {
        width: theme.spacing(9),
      },
    }),
  },
}));

const DataDisplay = () => {
  const [open, setOpen] = React.useState(true);
  const toggleDrawer = () => {
    setOpen(!open);
  };
  const navigate = useNavigate();
  const logout = () => {
    // Perform logout logic here
    navigate("/");
  };
  const [treeviewData, setTreeviewData] = useState([]);
  const [editingNodeId, setEditingNodeId] = useState(null);
  const [editingLabel, setEditingLabel] = useState("");
  const BASE_URL = useContext(BaseUrlContext);
  const [selectedClassNames, setSelectedClassNames] = useState([]);

  const [imageView, setimageView] = useState([]);
  const ImageView = ({ imagePaths }) => (
    <div>
      {imagePaths &&
        imagePaths.map((imagePath, index) => <p key={index}>{imagePath}</p>)}
    </div>
  );

  const ImageThumbnail = ({ imagePaths }) => (
    <div>
      {imagePaths &&
        imagePaths.map((imagePath, index) => (
          <img key={index} src={imagePath} alt="Thumbnail" />
        ))}
    </div>
  );

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    try {
      const response = await axios.get(`${BASE_URL}/treeview`);
      setTreeviewData(response.data.treeviewData);
      console.log("데이터 가져오기 성공:", response.data.treeviewData);
      console.log("데이터 가져오기 성공 data:", response.data);
      // console.log(
      //   "데이터 가져오기 성공 data.children:",
      //   response.data.treeviewData[0].children
      // );
    } catch (error) {
      console.error("데이터 가져오기 실패:", error);
    }
  };

  const handleEditNode = (nodeId, label) => {
    setEditingNodeId(nodeId);
    setEditingLabel(label);
  };

  const handleSaveNodeLabel = async (label, depth) => {
    try {
      console.log(`${label}를 ${editingLabel}로 수정하고 뎁스는 ${depth}`);
      const response = await axios.post(
        `${BASE_URL}/api/data/update/${depth}/${label}/${editingLabel}`,
        {
          depth: depth,
          label: label,
          editingLabel: editingLabel,
        }
      );
      console.log("백에서의 응답", response.data);
      setEditingNodeId(null);
      fetchData();
    } catch (error) {
      console.error("Label update failed:", error);
    }
  };

  const handleCancel = () => {
    setEditingNodeId(null);
  };

  const handleInputChange = (event) => {
    setEditingLabel(event.target.value);
  };
  const handleShowSelectedValues = () => {
    console.log("Selected Class Names:", selectedClassNames);
    handleDownloadCSV();
  };

  const handleDownloadCSV = async () => {
    try {
      const response = await axios.post(
        `${BASE_URL}/api/download-csv`,
        selectedClassNames,
        { responseType: "blob" }
      );

      const csvData = new Blob([response.data], { type: "text/csv" });
      const csvUrl = URL.createObjectURL(csvData);
      const link = document.createElement("a");
      link.href = csvUrl;
      link.setAttribute("download", "data.csv");
      document.body.appendChild(link);
      link.click();
      document.body.removeChild(link);
    } catch (error) {
      console.error("CSV download failed:", error);
    }
  };
  const renderTree = (data) => (
    <TreeView
      defaultCollapseIcon={<ExpandMoreIcon />}
      defaultExpandIcon={<ChevronRightIcon />}
    >
      {createTreeItems(data)}
    </TreeView>
  );

  const createTreeItems = (data) =>
    data.map((node) => (
      <TreeItem
        key={node.text}
        nodeId={node.text}
        label={createNodeLabel(node)}
        onClick={() => {
          handleTreeItemClick(node.text, node.Depth);
          console.log(
            "클릭한 애 node.text:",
            node.text,
            " 클릭한 애 node.children :",
            node.children,
            " 클릭한 애 node :",
            node,
            " 클릭한 애 node.Depth :",
            node.Depth
          );
        }}
      >
        {node.children && createTreeItems(node.children)}
      </TreeItem>
    ));
  const handleTreeItemClick = async (value, depth) => {
    console.log("value:", value, "depth:", depth);
    if (depth === "ClassName") {
      try {
        // Pass the value to the backend
        const response = await axios.post(
          `${BASE_URL}/api/treeitem-click/${value}`
        );
        // const imagePaths = response.data.image_urls;
        setimageView(response.data.image_urls);
        //setimageView(response.data);
        console.log("TreeItem click response:", response.data);
      } catch (error) {
        console.error("TreeItem click failed:", error);
      }
    }
    console.log("setimageView 후 :", imageView);
  };

  const handleToggleClassName = (className) => {
    const isSelected = selectedClassNames.includes(className);
    let updatedSelectedClassNames = [];

    if (isSelected) {
      updatedSelectedClassNames = selectedClassNames.filter(
        (name) => name !== className
      );
    } else {
      updatedSelectedClassNames = [...selectedClassNames, className];
    }

    setSelectedClassNames(updatedSelectedClassNames);
    console.log("selectedClassNames 안 ", selectedClassNames);
  };
  const createNodeLabel = (node) => (
    <Box display="flex" alignItems="center">
      <Checkbox
        //checked={selectedClassNames.includes(nodes.text)}
        onChange={() => handleToggleClassName(node.text)}
      />
      {editingNodeId === node.text ? (
        <Box display="flex" alignItems="center">
          <TextField
            label="Edited Label"
            value={editingLabel}
            onChange={handleInputChange}
          />
          <Button
            onClick={() => {
              handleSaveNodeLabel(node.text, node.Depth);
              console.log("node.Depth", node.Depth);
            }}
          >
            Save
          </Button>
          <Button onClick={handleCancel}>Cancel</Button>
        </Box>
      ) : (
        <React.Fragment>
          <span>{node.text}</span>
          <Button onClick={() => handleEditNode(node.text, node.text)}>
            Edit
          </Button>
        </React.Fragment>
      )}
    </Box>
  );

  return (
    <Box sx={{ display: "flex" }}>
      <CssBaseline />
      <AppBar position="absolute" open={open}>
        <Toolbar
          sx={{
            pr: "24px", // keep right padding when drawer closed
          }}
        >
          <IconButton
            edge="start"
            color="inherit"
            aria-label="open drawer"
            onClick={toggleDrawer}
            sx={{
              marginRight: "36px",
              ...(open && { display: "none" }),
            }}
          >
            <MenuIcon />
          </IconButton>
          <Typography
            component="h1"
            variant="h6"
            color="inherit"
            noWrap
            sx={{ flexGrow: 1 }}
            style={{ fontFamily: "KIMM_Bold" }}
          >
            Erebor
          </Typography>
          <IconButton onClick={logout}>
            <LogoutIcon color="secondary" />
          </IconButton>
        </Toolbar>
      </AppBar>
      <Drawer variant="permanent" open={open}>
        <Toolbar
          sx={{
            display: "flex",
            alignItems: "center",
            justifyContent: "flex-end",
            px: [1],
          }}
        >
          <IconButton onClick={toggleDrawer}>
            <ChevronLeftIcon />
          </IconButton>
        </Toolbar>
        <Divider />
        <List component="nav">
          <MainListItems />
          <Divider sx={{ my: 1 }} />
        </List>
      </Drawer>
      <Box
        component="main"
        sx={{
          backgroundColor: (theme) =>
            theme.palette.mode === "light"
              ? theme.palette.grey[100]
              : theme.palette.grey[900],
          flexGrow: 1,
          height: "100vh",
          overflow: "auto",
        }}
      >
        <Toolbar />
        <Container maxWidth="lg" sx={{ mt: 4, mb: 4 }}>
          <Paper
            sx={{
              p: 2,
              display: "flex",
              flexDirection: "column",
              height: 5000,
            }}
          >
            <Box display="flex" flexDirection="row">
              <Box width="50%">
                <h1>Class List</h1>
                <Button onClick={handleShowSelectedValues}>
                  선택된 클래스 콘솔에 찍어보기 및 다운로드
                </Button>
                {renderTree(treeviewData)}
              </Box>
              <Box width="50%">
                {/*오른쪽 페이지 */}
                <h1>Image Display</h1>
                {/* <ImageView imagePaths={imageView} /> */}
                {/* {imageView} */}
                <Grid container spacing={2}>
                  {imageView &&
                    imageView.map((imagePath, index) => (
                      //<img key={index} src={imagePath} alt={`Image ${index}`} />
                      <Grid item xs={4} key={index}>
                        <Card
                          key={index}
                          variant="outlined"
                          sx={{
                            width: "100%",
                            height: "auto",
                            display: "flex",
                            flexDirection: "column",
                            my: 1,
                          }}
                        >
                          <CardActions>
                            {index + 1}
                            <Typography>.</Typography>
                          </CardActions>
                          <CardMedia
                            component="img"
                            src={imagePath}
                            alt={`Image ${index}`}
                            height={200} // 원하는 이미지 높이로 설정합니다.
                          />
                          {/* <CardContent>
                             추가적인 내용이 있다면 여기에 표시할 수 있습니다. 
                          </CardContent> */}
                        </Card>
                        {/* <Card
                          variant="outlined"
                          sx={{
                            width: "100%",
                            height: "auto",
                            display: "flex",
                            flexDirection: "column",
                            my: 1,
                          }}
                        >
                          <CardActions>
                            {index + 1}
                            <Typography>.</Typography>
                          </CardActions>
                          <Typography>{imagePath}</Typography>
                        </Card> */}
                      </Grid>
                    ))}
                </Grid>
              </Box>
            </Box>
          </Paper>
        </Container>
        <Copyright sx={{ pt: 4 }} />
      </Box>
    </Box>
    // <div>
    //   <h1>Data Display</h1>
    //   <Button onClick={handleShowSelectedValues}>선택된 클래스 콘솔에 찍어보기 및 다운로드</Button>
    //   {renderTree(treeviewData)}
    // </div>
  );
};

export default DataDisplay;
