import React, { useState, useMemo } from 'react';
import TextField from '@mui/material/TextField';
import Table from '@mui/material/Table';
import TableBody from '@mui/material/TableBody';
import TableCell from '@mui/material/TableCell';
import TableHead from '@mui/material/TableHead';
import TableRow from '@mui/material/TableRow';
import Modal from '@mui/material/Modal';
import IconButton from '@mui/material/IconButton';
import FilterListIcon from '@mui/icons-material/FilterList';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';

const VISIBLE_FIELDS = ['model', 'depth1', 'depth2', 'depth3', 'tag', 'brand'];

const data = [
  { id: 1, model: 'T800', depth1: '식품', depth2: '음료', depth3: '건강/에너지음료', tag: '병음료', brand: '동아제약' },
  { id: 2, model: 'T800', depth1: '식품', depth2: '음료', depth3: '건강/에너지음료', tag: '병음료', brand: '동아제약' },
  { id: 3, model: 'T800', depth1: '식품', depth2: '음료', depth3: '탄산음료', tag: '패트병', brand: '롯데' },
  { id: 4, model: 'T800', depth1: '식품', depth2: '음료', depth3: '커피음료', tag: '뚜껑,캔음료', brand: '롯데' },
  { id: 5, model: 'T800', depth1: '식품', depth2: '음료', depth3: '탄산음료', tag: '패트병', brand: '롯데' },
  { id: 6, model: 'T800', depth1: '식품', depth2: '음료', depth3: '이온음료', tag: '패트병', brand: '코카콜라' },
  { id: 7, model: 'T800', depth1: '뷰티', depth2: '메이크업', depth3: '파운데이션', tag: '팩트형', brand: '클리오' },
  { id: 8, model: 'T800', depth1: '뷰티', depth2: '바디케어', depth3: '바디로션', tag: '펌프용기', brand: '클리오' },
  // ... additional data
];

export default function MultiFilterDataGrid() {
  const [filterValues, setFilterValues] = useState({});
  const [modalOpen, setModalOpen] = useState(false);
  const [currentField, setCurrentField] = useState('');
  const [tempFilterValues, setTempFilterValues] = useState({});

  const handleFilterChange = (field, value) => {
    setFilterValues((prevValues) => ({
      ...prevValues,
      [field]: value,
    }));
  };

  const handleModalOpen = (field) => {
    setCurrentField(field);
    setTempFilterValues(filterValues); // 필터링 값을 임시로 저장
    setModalOpen(true);
  };

  const handleModalClose = () => {
    setModalOpen(false);
    setFilterValues(tempFilterValues); // 임시로 저장한 필터링 값을 복원
  };

  const filteredData = useMemo(() => {
    if (Object.keys(filterValues).length === 0) {
      return data;
    }

    return data.filter((row) => {
      return Object.entries(filterValues).every(([field, filters]) => {
        const rowValue = row[field].toLowerCase();
        return filters.some((filter) => rowValue.includes(filter));
      });
    });
  }, [filterValues]);

  const handleConfirmFilter = () => {
    setModalOpen(false);
    setTempFilterValues({}); // 임시로 저장한 필터링 값을 초기화
  };

  return (
    <div>
      <Table>
        <TableHead>
          <TableRow>
            {VISIBLE_FIELDS.map((field) => (
              <TableCell key={field}>
                <Box display="flex" alignItems="center">
                  {field}
                  <IconButton onClick={() => handleModalOpen(field)}>
                    <FilterListIcon />
                  </IconButton>
                </Box>
              </TableCell>
            ))}
          </TableRow>
        </TableHead>
        <TableBody>
          {filteredData.map((row) => (
            <TableRow key={row.id}>
              {VISIBLE_FIELDS.map((field) => (
                <TableCell key={`${row.id}-${field}`}>{row[field]}</TableCell>
              ))}
            </TableRow>
          ))}
        </TableBody>
      </Table>
      <Modal open={modalOpen} onClose={handleModalClose}>
        <Box
          sx={{
            position: 'absolute',
            top: '50%',
            left: '50%',
            transform: 'translate(-50%, -50%)',
            bgcolor: 'background.paper',
            border: '2px solid #000',
            boxShadow: 24,
            p: 4,
          }}
        >
          <TextField
            value={filterValues[currentField] || ''}
            onChange={(e) =>
              handleFilterChange(currentField, e.target.value.split(',').map((filter) => filter.trim().toLowerCase()))
            }
            label={currentField}
            variant="outlined"
            size="small"
            fullWidth
            autoFocus
          />
          <Box display="flex" justifyContent="flex-end" mt={2}>
            <Button onClick={handleConfirmFilter}>확인</Button>
            <Button onClick={handleModalClose}>닫기</Button>
          </Box>
        </Box>
      </Modal>
    </div>
  );
}
