const getTreeItemsFromData = (
  data,
  path = "",
  setPath,
  setNewname,
  checkedItems,
  setCheckedItems,
  handleTextClick
) => {
  const handleCheckboxChange = (event, item) => {
    const isChecked = event.target.checked;
    const checkedItemIndex = checkedItems.findIndex(
      (checkedItem) => checkedItem.name === item.name
    );

    if (isChecked && checkedItemIndex === -1) {
      setCheckedItems([...checkedItems, item]);
    } else if (!isChecked && checkedItemIndex !== -1) {
      checkedItems.splice(checkedItemIndex, 1);
      setCheckedItems([...checkedItems]);
    }
  };

  return data.map((item) => {
    const currentPath = `${path}/${item.name}`;
    //console.log("editMode : "+editMode);
    if (item.children) {
      //여기에 if (item.type == files)이러면 썸네일 보여주게 들어가야할듯
      return (
        <TreeItem
          key={currentPath}
          nodeId={currentPath}
          label={
            <>
              <Checkbox
                checked={checkedItems.some(
                  (checkedItem) => checkedItem.name === item.name
                )}
                onChange={(event) => handleCheckboxChange(event, item)}
              />
              {item.name}
            </>
          }
          onClick={() => {
            setPath(currentPath);
            setNewname(item.name);
            console.log(
              "currentPath : " + currentPath + " | Newname : " + item.name
            );
          }}
        >
          {getTreeItemsFromData(
            item.children,
            currentPath,
            setPath,
            setNewname,
            checkedItems,
            setCheckedItems,
            handleTextClick
          )}
        </TreeItem>
      );
    }
    return (
      <TreeItem
        key={currentPath}
        nodeId={currentPath}
        label={
          <>
            <Checkbox
              checked={checkedItems.some(
                (checkedItem) => checkedItem.name === item.name
              )}
              onChange={(event) => handleCheckboxChange(event, item)}
            />
            {item.name}
          </>
        }
        onClick={() => {
          setPath(currentPath);
          setNewname(item.name);
          handleTextClick();
          console.log(
            "currentPath2 : " + currentPath + " | Newname2 : " + item.name
          );
        }}
      />
    );
  });
};

const ThumbnailCard = () => {
  const [open, setOpen] = useState(false);

  const classes = useStyles();
  const [data, setData] = useState({});
  //마우스 오버 ->상세 데이터 보여주기
  const [anchorEl, setAnchorEl] = useState(false);

  const handlePopoverOpen = (event) => {
    setAnchorEl(event.currentTarget);
  };

  const handlePopoverClose = () => {
    setAnchorEl(false);
  };

  const dataopen = Boolean(anchorEl);

  useEffect(() => {
    axios.get("http://localhost:3001/product").then((response) => {
      setData(response.data);
      console.log(response.data, "response.data");
    });
  }, []);
  return (
    <Paper
      sx={{
        p: 2,
        display: "flex",
        flexDirection: "column",
        variant: "outlined",
        height: 1500,
      }}
    >
      <div className={classes.root}>
        <Grid container spacing={2}>
          {Object.values(data).map((img) => (
            <Card
              variant="outlined"
              key={img.src}
              sx={{
                width: "15%",
                hight: "auto",
                display: "flex",
                flexDirection: "column",
                my: 1,
              }}
              aria-owns={dataopen ? "mouse-over-popover" : undefined}
              aria-haspopup="true"
              onMouseEnter={handlePopoverOpen}
              onMouseLeave={handlePopoverClose}
            >
              <CardActions>
                {img.id}
                <Typography level="h2" fontSize="md">
                  .
                </Typography>
              </CardActions>
              <img src={img.src} alt="img" height="auto" width="100%" />
            </Card>
          ))}
          <Popover
            id="mouse-over-popover"
            sx={{
              pointerEvents: "none",
            }}
            open={dataopen}
            anchorEl={anchorEl}
            anchorOrigin={{
              vertical: "bottom",
              horizontal: "left",
            }}
            transformOrigin={{
              vertical: "top",
              horizontal: "left",
            }}
            onClose={handlePopoverClose}
            disableRestoreFocus
          >
            <Typography sx={{ p: 1 }}>I use Popover.</Typography>
          </Popover>
        </Grid>
      </div>
    </Paper>
  );
};

function TreeViewExample(props) {
  const [open, setOpen] = React.useState(true);
  const [path, setPath] = useState("");
  const [newname, setNewname] = useState("");
  const [editMode, setEditMode] = React.useState(false);
  const [checkedItems, setCheckedItems] = useState([]);
  const [messages, setMessages] = useState([]);

  const [showThumbnail, setShowThumbnail] = useState(false);

  const handleTextClick = () => setShowThumbnail(true);

  const toggleDrawer = () => {
    setOpen(!open);
  };
  const navigate = useNavigate();
  const logout = () => {
    // Perform logout logic here
    navigate("/");
  };
  const [anchorEl, setAnchorEl] = React.useState(null);
  const open2 = Boolean(anchorEl);
  const handleClick = (event) => {
    setAnchorEl(event.currentTarget);
    //console.log(anchorEl);
  };
  const handleClose = () => {
    setAnchorEl(null);
  };
  useEffect(() => {
    axios.get('http://localhost:8000/api/data').then(response => {
      setMessages(response.data);
      console.log("DB에 있는거 : ",messages)
    });
  }, []);
  return (
    <Box sx={{ display: "flex" }}>
      <CssBaseline />

      <Box
        component="main"
        sx={{
          backgroundColor: (theme) =>
            theme.palette.mode === "light"
              ? theme.palette.grey[100]
              : theme.palette.grey[900],
          flexGrow: 1,
          height: "100vh",
          overflow: "auto",
        }}
      >
        <Toolbar />
        <Container maxWidth="lg" sx={{ mt: 4, mb: 4 }}>
          <Paper
            sx={{
              p: 2,
              display: "flex",
              flexDirection: "column",
              height: 2000,
            }}
          >
            <Toolbar>
              <Typography variant="h5" gutterBottom noWrap sx={{ flexGrow: 1 }}>
                {(path && <div>현재 경로 : {path}</div>) || "현재 경로 : "}
              </Typography>
            </Toolbar>
            <Box
              display="flex"
              justifyContent="flex-end"
              alignItems="flex-end"
              flexDirection="row"
            >
              <Button
                sx={{ width: 100, mr: 2 }}
                id="basic-button"
                aria-controls={open2 ? "basic-menu" : undefined}
                aria-haspopup="true"
                aria-expanded={open2 ? "true" : undefined}
                onClick={handleClick}
                variant="contained"
              >
                다운로드
              </Button>
              <Menu
                id="basic-menu"
                anchorEl={anchorEl}
                open={open2}
                onClose={handleClose}
                MenuListProps={{
                  "aria-labelledby": "basic-button",
                }}
              >
                <MenuItem
                  onClick={() => {
                    handleClose();
                    console.log("경로다운로드 :" + path);
                  }}
                >
                  경로 다운로드
                </MenuItem>
                <MenuItem
                  onClick={() => {
                    handleClose();
                    console.log("클래스 이동 :" + newname);
                    setEditMode(!editMode);
                  }}
                >
                  클래스 이동
                </MenuItem>
                <MenuItem
                  onClick={() => {
                    handleClose();
                    console.log("삭제 :" + newname);
                    setEditMode(!editMode);
                  }}
                >
                  삭제
                </MenuItem>
              </Menu>
            </Box>

            <Box display="flex" flexDirection="row">
              <Box width="50%">
                <Paper
                  sx={{
                    p: 2,
                    display: "flex",
                    flexDirection: "column",
                    variant: "outlined",
                    height: 1500,
                  }}
                >
                  <TreeView
                    defaultCollapseIcon={<ExpandMoreIcon />}
                    defaultExpandIcon={<ChevronRightIcon />}
                  >
                    {getTreeItemsFromData(
                      sampleData,
                      "",
                      setPath,
                      setNewname,
                      checkedItems,
                      setCheckedItems,
                      handleTextClick
                    )}
                    {/* {editMode 
                ? getTreeItemsFromData(sampleData, "", setPath,setNewname)
                : getTreeItemsFromEditData(sampleData, "", setNewname)
              }  */}
                  </TreeView>
                </Paper>
              </Box>
              <Box width="50%">
                {/* 오른쪽 paper */}
                {showThumbnail && <ThumbnailCard />}
              </Box>
            </Box>
          </Paper>
          <Copyright sx={{ pt: 4 }} />
        </Container>
      </Box>
    </Box>
  );
}

export default TreeViewExample;
