import * as React from 'react';
import { useNavigate } from "react-router-dom";
import ListItemButton from '@mui/material/ListItemButton';
import ListItemIcon from '@mui/material/ListItemIcon';
import ListItemText from '@mui/material/ListItemText';
import ListSubheader from '@mui/material/ListSubheader';
import DashboardIcon from '@mui/icons-material/Dashboard';
import FolderIcon from '@mui/icons-material/Folder';
import PeopleIcon from '@mui/icons-material/People';
import BarChartIcon from '@mui/icons-material/BarChart';
import LayersIcon from '@mui/icons-material/Layers';
import List from '@mui/material/List';


function MainListItems (){
  
const navigate = useNavigate();

const onClickDashboard = () => {
  navigate("/dashboard");
};
const onClickFile = () => {
  navigate("/file");
};
const onClickImg = () => {
  navigate("/img");
};
const onClickSearch = () => {
  navigate("/tabs");
};
const onClickUpdate = () => {
  navigate("/messages");
};
const onClickNas = () => {
  navigate("/nasdata");
};


  return (
    <div>
    <List component="nav">
    <ListItemButton onClick={onClickDashboard}>
      <ListItemIcon>
        <DashboardIcon />
      </ListItemIcon>
      <ListItemText primary="Dashboard" />
    </ListItemButton>
    <ListItemButton onClick={onClickFile}>
      <ListItemIcon>
        <FolderIcon />
      </ListItemIcon>
      <ListItemText primary="FileSystem" />
    </ListItemButton>
    
    <ListItemButton onClick={onClickImg}>
      <ListItemIcon>
        <DashboardIcon />
      </ListItemIcon>
      <ListItemText primary="img" />
    </ListItemButton>
    
    <ListItemButton onClick={onClickSearch}>
      <ListItemIcon>
        <DashboardIcon />
      </ListItemIcon>
      <ListItemText primary="search" />
    </ListItemButton>
    <ListItemButton onClick={onClickNas}>
      <ListItemIcon>
        <DashboardIcon />
      </ListItemIcon>
      <ListItemText primary="NasData" />
    </ListItemButton>
    </List>
  </div>
);
}
export default MainListItems;