import React, { useContext, useEffect, useState } from "react";
import axios from "axios";
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
} from "@mui/material";
import { BaseUrlContext } from "../../index";

const YourComponent = () => {
  const [data, setData] = useState([]);
  const BASE_URL = useContext(BaseUrlContext);

  useEffect(() => {
    const fetchData = async () => {
      try {
        const response = await axios.get(
          `${BASE_URL}/api/data/count-by-classname`
        );
        setData(response.data.result);
        console.log("데이터 가져오기 성공  response.data:", response.data);
        //response.data: {result: Array(3)}
        console.log(
          "데이터 가져오기 성공  response.data.result:",
          response.data.result
        );
        //response.data.result: (3) [{…}, {…}, {…}]
      } catch (error) {
        console.error(error);
      }
    };

    fetchData();
  }, []);

  return (
    <TableContainer>
      <Table>
        <TableHead>
          <TableRow>
            <TableCell>ClassName</TableCell>
            <TableCell>ImageCount</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {Array.isArray(data) &&
            data.map((item) => (
              <TableRow key={item._id}>
                <TableCell>{item._id}</TableCell>
                <TableCell>{item.count}</TableCell>
              </TableRow>
            ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
};

export default YourComponent;
