import React, { useState, useEffect } from "react";
import axios from "axios";

const App = () => {
  const [cookies, setCookies] = useState([]);
  const [name, setName] = useState("");
  const [description, setDescription] = useState("");
  const [image_url, setImage_Url] = useState("");
  const [price, setPrice] = useState(0);

  const getCookies = async () => {
    const response = await axios.get("http://localhost:8000/cookies");
    setCookies(response.data);
  };

  useEffect(() => {
    getCookies();
  }, []);

  // const handleSubmit = async (e) => {
  //   e.preventDefault();
  //   const newCookie = {
  //     name,
  //     description,
  //     imageUrl,
  //     price: Number(price)  //Number 형식을 맞추기 위해서 필요
  //   };
  //   await axios.post('http://localhost:8000/cookies', newCookie);
  //   getCookies();
  //   setName('');
  //   setDescription('');
  //   setImageUrl('');
  //   setPrice(0);
  // };
  const handleSubmit = async (e) => {
    e.preventDefault();
    const newCookie = {
      name,
      description,
      image_url,
      price: Number(price), //Number 형식을 맞추기 위해서 필요
    };
    try {
      await axios.post("http://localhost:8000/cookies", newCookie, {
        headers: {
          "Content-Type": "application/json",
        },
      });
      setName("");
      setDescription("");
      setImage_Url("");
      setPrice(0);
      getCookies();
    } catch (error) {
      console.error(error);
    }
  };

  return (
    <div>
      <h1>쿠키 메뉴</h1>
      <ul>
        {cookies.map((cookie) => (
          <li key={cookie._id}>
            <h2>{cookie.name}</h2>
            <p>{cookie.description}</p>
            <img src={cookie.image_url} alt={cookie.name} />
            <p>{cookie.price}원</p>
          </li>
        ))}
      </ul>

      <form onSubmit={handleSubmit}>
        <input
          type="text"
          value={name}
          onChange={(e) => setName(e.target.value)}
          placeholder="쿠키 이름"
          required
        />
        <br />
        <input
          type="text"
          value={description}
          onChange={(e) => setDescription(e.target.value)}
          placeholder="쿠키 설명"
        />
        <br />
        <input
          type="text"
          value={image_url}
          onChange={(e) => setImage_Url(e.target.value)}
          placeholder="이미지 URL"
        />
        <br />
        <input
          type="number"
          value={price}
          onChange={(e) => setPrice(Number(e.target.value))}
          placeholder="가격"
          required
        />
        <br />
        <button type="submit">추가하기</button>
      </form>
    </div>
  );
};

export default App;
