import React, { useState, useEffect } from 'react';

function App() {
  const [data, setData] = useState([]);

  useEffect(() => {
    fetchData();
  }, []);

  const fetchData = async () => {
    try {
      const response = await fetch('http://localhost:8000/api/data');
      const jsonData = await response.json();
      setData(jsonData);
    } catch (error) {
      console.log('Error fetching data:', error);
    }
  };

  return (
    <div>
      <h1>Data from MongoDB</h1>
      <ul>
        {data.map((item) => (
          <li key={item._id}>
            <h3>Image Name: {item['Image Name']}</h3>
            <p>Image Path: {item['combined Data']['image_path']}</p>
            <p>GT Crop: {JSON.stringify(item['combined Data']['gt_crop'])}</p>
            <p>Info Crop: {JSON.stringify(item['combined Data']['info_crop'])}</p>
            <p>Depth: {JSON.stringify(item['combined Data']['depth'])}</p>
          </li>
        ))}
      </ul>
    </div>
  );
}

export default App;
