import React, { useState } from 'react';

const rows = [
  { id: 1, name: 'John', rating: 4.5, country: 'USA', dateCreated: '2021-01-01', isAdmin: true },
  { id: 2, name: 'Jane', rating: 3.2, country: 'Canada', dateCreated: '2020-12-25', isAdmin: false },
  { id: 3, name: 'Bob', rating: 5.0, country: 'Australia', dateCreated: '2022-02-14', isAdmin: true },
  // ...add more rows as needed
];

const columns = [
  { field: 'name', headerName: 'Name', width: 150 },
  { field: 'rating', headerName: 'Rating', type: 'number', width: 120 },
  { field: 'country', headerName: 'Country', width: 150 },
  { field: 'dateCreated', headerName: 'Date Created', width: 150 },
  { field: 'isAdmin', headerName: 'Is Admin', type: 'boolean', width: 120 },
  // ...add more columns as needed
];

export default function BasicExampleDataGrid() {
  const [filteredRows, setFilteredRows] = useState(rows);
  const [filters, setFilters] = useState({});

  function handleFilterChange(e) {
    const newFilters = { ...filters, [e.target.name]: e.target.value };
    setFilters(newFilters);

    const filteredData = rows.filter((row) => {
      for (let filterKey in newFilters) {
        const filterValue = newFilters[filterKey];
        const rowValue = row[filterKey];
        if (filterValue !== '' && String(rowValue).toLowerCase().indexOf(filterValue.toLowerCase()) === -1) {
          return false;
        }
      }
      return true;
    });
    setFilteredRows(filteredData);
  }

  return (
    <div style={{ height: 400, width: '100%' }}>
      <div style={{ marginBottom: '10px' }}>
        <label>Name:</label>
        <input type="text" name="name" onChange={handleFilterChange} />
        <label>Country:</label>
        <input type="text" name="country" onChange={handleFilterChange} />
        <label>Is Admin:</label>
        <select name="isAdmin" onChange={handleFilterChange}>
          <option value="">All</option>
          <option value="true">Yes</option>
          <option value="false">No</option>
        </select>
      </div>
      <table style={{ borderCollapse: 'collapse', width: '100%' }}>
        <thead>
          <tr>
            {columns.map((column) => (
              <th key={column.field} style={{ border: '1px solid black', padding: '5px' }}>
                {column.headerName}
              </th>
            ))}
          </tr>
        </thead>
        <tbody>
          {filteredRows.map((row) => (
            <tr key={row.id}>
              {columns.map((column) => (
                <td key={column.field} style={{ border: '1px solid black', padding: '5px' }}>
                  {column.type === 'boolean' ? (
                    row[column.field] ? 'Yes' : 'No'
                  ) : (
                    row[column.field]
                  )}
                </td>
              ))}
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
