import * as React from "react";
import Link from "@mui/material/Link";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Title from "../title";

// Generate Order Data
function createData(type, origin_class, clean_class, origin_img, clean_img) {
  return { type, origin_class, clean_class, origin_img, clean_img };
}

const rows = [
  createData("Crowdworks", 254798, 254798, 10172389, 10172389),
  createData("Data_center", 12616, 12616, 1965277, 1965277),
  createData("Event", 5173, 5173, 7228484, 7228484),
  createData("MOU", 8274, 8274, 3722837, 3722837),
  createData("Office", 6651, 6651, 485512, 485512),
  createData("Open_data", 184, 184, 867196, 867196),
  createData("Test_dataset", 249269, 249269, 7228484, 7228484),
  createData("Tuna", 38238, 38238, 3602784, 3602784),
  createData("User_data", 287507, 287507, 485512, 485512),
];

function preventDefault(event) {
  event.preventDefault();
}

export default function Orders() {
  return (
    <React.Fragment>
      <Title>데이터 현황</Title>
      <Table size="small">
        <TableHead>
          <TableRow>
            <TableCell>type</TableCell>
            <TableCell>origin_class</TableCell>
            <TableCell>clean_class</TableCell>
            <TableCell>origin_img</TableCell>
            <TableCell>clean_img</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow key={row.type}>
              <TableCell>{row.type}</TableCell>
              <TableCell>{row.origin_class}</TableCell>
              <TableCell>{row.clean_class}</TableCell>
              <TableCell>{row.origin_img}</TableCell>
              <TableCell>{row.clean_img}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
      <Link color="primary" href="#" onClick={preventDefault} sx={{ mt: 3 }}>
        See more orders
      </Link>
    </React.Fragment>
  );
}
