import React, { useState, useEffect } from 'react';
import axios from 'axios';

function App() {
  const [messages, setMessages] = useState([]);
  const [editingMessageId, setEditingMessageId] = useState(null);

  useEffect(() => {
    fetchMessages();
  }, []);

  function fetchMessages() {
    axios
      .get('http://localhost:8000/messages')
      .then(response => {
        setMessages(response.data);
      })
      .catch(error => {
        console.error(error);
      });
  }

  function handleEditSave(id, text) {
    if (!id) {
      console.error("Invalid message id:", id);
      return;
    }
  
    const updatedMessages = messages.map(message => {
      if (message._id === id) {
        return { ...message, text: text };
      }
      return message;
    });
  
    axios
      .put(`http://localhost:8000/messages/${id}`, { text })
      .then(() => {
        setMessages(updatedMessages);
        setEditingMessageId(null);
      })
      .catch(error => {
        console.error(error);
      });
  }
  
  function handleCancelEdit() {
    setEditingMessageId(null);
  }

  function handleAddMessage(message) {
    axios
      .post('http://localhost:8000/messages', { text: message })
      .then(response => {
        
        console.log( response.data)
        const newMessage = response.data; 
        console.log(newMessage)
        setMessages(prevMessages => [...prevMessages, newMessage]);
      })
      .catch(error => {
        console.error(error);
      });
  }

  return (
    <div>
      {messages.map(message => (
        <div key={message._id}> 
          {editingMessageId === message._id ? (
            <EditMessage
              id={message._id}
              initialText={message.text}
              onSave={handleEditSave}
              onCancel={handleCancelEdit}
            />
          ) : (
            <div>
              <div>{message.text}</div>
              <button onClick={() => setEditingMessageId(message._id)}>Edit</button>
            </div>
          )}
        </div>
      ))}
      <NewMessageForm onAdd={handleAddMessage} />
    </div>
  );
}

function EditMessage({ id, initialText, onSave, onCancel }) {
  const [text, setText] = useState(initialText);

  function handleSubmit(event) {
    event.preventDefault();
    onSave(id, text);
  }

  return (
    <form onSubmit={handleSubmit}>
      <input value={text} onChange={event => setText(event.target.value)} />
      <button type="submit">Save</button>
      <button type="button" onClick={onCancel}>
        Cancel
      </button>
    </form>
  );
}

function NewMessageForm({ onAdd }) {
  const [text, setText] = useState('');

  function handleSubmit(event) {
    event.preventDefault();
    onAdd(text);
    setText('');
  }

  return (
    <form onSubmit={handleSubmit}>
      <input value={text} onChange={event => setText(event.target.value)} />
      <button type="submit">Add</button>
    </form>
  );
}

export default App;
